@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.header{background-color:transparent;position:absolute;top:0;left:0;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;font-family:"PTSans",sans-serif;font-size:1.5rem;color:#f1f1f1;z-index:10}.header::after{content:"";position:absolute;bottom:0;width:calc(100% - 132px);height:1px;background-color:#f1f1f1}@media screen and (max-width: 425px){.header::after{display:none}}.header__menu{position:absolute;top:40px;left:40px;display:none;z-index:5}.header__menu.active+.header__content{transform:translateX(0)}@media screen and (max-width: 425px){.header__menu{display:block}}@media screen and (max-width: 425px){.header__content{width:100%;background-color:#00000080;transform:translateX(-100%);transition:transform .3s ease-in}}
.menu{display:flex;align-items:center;font-weight:bold;text-transform:uppercase}.menu__open span{padding:0 5px}.menu__close{margin-left:1rem;display:none}.menu.active .menu__open{display:none}.menu.active .menu__close{display:block}
.nav{padding:40px 0;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;grid-gap:48px;gap:48px;text-transform:uppercase}.nav__link{position:relative;text-decoration:none;color:inherit}.nav__link::after{content:"";position:absolute;bottom:-0.5rem;left:0;width:0%;height:1px;background-color:#f1f1f1;background-color:var(--color-link, #f1f1f1);transition:all .2s ease-in}.nav__link:hover::after{width:100%}@media screen and (max-width: 425px){.nav{flex-direction:column;width:100%;grid-gap:25px;gap:25px}}
.intro{position:relative;width:100%;height:100vh;background-image:url(../../static/media/Background.fdca32cc.png);background-size:cover;background-position:center;display:flex;flex-direction:column;justify-content:flex-end;color:#f1f1f1;text-transform:uppercase}@media screen and (max-width: 768px){.intro__hint{display:none}}@media screen and (max-width: 425px){.intro{height:267px;background-image:url(../../static/media/Background-mobile.4e96a025.png)}}
.hint-to-scroll{margin-bottom:55px}.hint-to-scroll__hint{margin-bottom:5px;font-family:"Open Sans",sans-serif;font-size:18px;letter-spacing:2px;text-transform:uppercase;color:inherit;opacity:.8}.hint-to-scroll__icon{margin:0 auto;width:21px;height:21px;border-left:3px solid #fff;border-bottom:3px solid #fff;transform:rotate(-45deg);fill:inherit;border-color:#ffffff;border-color:var(--color-border, #ffffff);animation:trail 1s infinite}.hint-to-scroll__icon+.hint-to-scroll__icon{margin-top:-10px;animation-delay:.3s}@media screen and (max-width: 425px){.hint-to-scroll{width:50%}}@keyframes trail{from{opacity:1}to{opacity:.4}}
.about-me{width:100%;background-color:#fff;color:#bebebe;--color-border: #bebebe}.about-me__slot{position:relative;font-size:24px;--color-link: #bebebe}.about-me__slot::after{content:"";position:absolute;bottom:0;left:66px;width:calc(100% - 132px);height:1px;background-color:#bebebe}@media screen and (max-width: 768px){.about-me__slot::after{width:100%;left:0}}@media screen and (max-width: 425px){.about-me__slot{display:none}}.about-me__inner{position:relative;margin:75px auto;display:flex;justify-content:center;flex-wrap:wrap}@media screen and (max-width: 768px){.about-me__inner{margin:0 0 30px}}.about-me__item{width:100%;max-width:771px;font-family:"Open Sans",sans-serif;text-align:left;color:#202020;overflow-x:hidden}.about-me__item+.about-me__item{margin-left:56px}@media screen and (max-width: 425px){.about-me__item+.about-me__item{margin-left:35px}}.about-me__item img{display:block;width:100%;height:auto}@media screen and (max-width: 425px){.about-me__item img{width:auto;height:700px;transform:translateX(-30%)}}.about-me__title{margin-top:20px;padding:60px 0;font-size:60px;text-transform:uppercase}@media screen and (max-width: 768px){.about-me__title{margin-top:0}}@media screen and (max-width: 425px){.about-me__title{padding:40px 0 0;font-size:30px}}.about-me__name{text-transform:capitalize;font-size:2.25rem;font-weight:bold}@media screen and (max-width: 425px){.about-me__name{font-size:1.12rem}}.about-me p{padding:12px 0;margin-bottom:20px;width:95%;max-width:680px;font-size:1.5rem;line-height:2}@media screen and (max-width: 425px){.about-me p{font-size:1.12rem;font-weight:300;line-height:2.5;width:90%}}@media screen and (max-width: 425px){.about-me p+p{display:none}}@media screen and (max-width: 425px){.about-me__hint{display:none}}@media screen and (max-width: 768px){.about-me{margin-bottom:85px}}@media screen and (max-width: 425px){.about-me{margin-bottom:45px}}
